* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.container {
  width: 1366px;
  height: 6000px;
  margin: 0 auto;
  padding: 20px 30px;
  background-color: #caf2f1;
  background-image: url(../images/background.png);
}

.top-header-block {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @div {
    color: red;
  }
}

